import Evento from './Evento';

const Eventos = (props) => {
    const eventoId = Object.keys(props.eventos);
    return ( 
        <div className="list-group">
            {eventoId.map(key => (
                <Evento 
                    key = {key}
                    evento = {props.eventos[key]}
                />
            ))}
        </div>
    );
}
 
export default Eventos;